




























































































































































import { Component, Mixins } from 'vue-property-decorator'
import getRepository from '@/services'
import ContractRepository from '@/services/repositories/contract'
import DetailContract from '../components/popup/DetailContract.vue'
// import ActionContract from '../components/popup/ActionContract.vue'
import AuditPopupFilter from '@/modules/audit/components/popup/AuditPopupFilter.vue'
import PopupMixin from '@/mixins/popup'
import { namespace } from 'vuex-class'
import ParamRepository from '@/services/repositories/params'
import ExportMixin from '../mixins/export'
// import { Sort } from '../interface'
import { forEach, assign } from 'lodash'
import { RELATIONSHIP_VALUE_TO_TYPE } from '@/configs'
import SearchBar from '../../audit/components/filters/Searchbar.vue'
import FilterMixin from '@/mixins/filterAuditContractMixin'

const beAudit = namespace('beAudit')

@Component({ components: { DetailContract, AuditPopupFilter, SearchBar } })
export default class ContractPage extends Mixins(PopupMixin, ExportMixin, FilterMixin) {
  @beAudit.Getter('getFilter') getFilter!: (rerport: string) => { contract: string; filters: Record<string, any> }

  api!: ContractRepository
  apiParam?: ParamRepository
  loading = false
  data: Array<Record<string, any>> = []
  // dataSelect: Array<Record<string, any>> = []
  valueSearch = ''
  query: Record<string, any> = {
    page: 1,
    search: '',
    limit: 20,
    total: 0,
    contractStatus: 3,
    searchType: 'APPLY_FORM',
    orderByClause: ''
  }
  contractInfor: Record<string, any> = {}
  isLoading = false
  option: Array<Record<string, any>> = [
    {
      id: 1,
      name: 'Đơn vị kinh doanh',
      divided: false
    },
    {
      id: 6,
      name: 'Tổng phí',
      divided: true
    },
    {
      id: 2,
      name: 'Ngày phát hành',
      divided: true
    },
    {
      id: 3,
      name: 'Ngày đóng phí',
      divided: false
    },
    {
      id: 4,
      name: 'Ngày hết hạn',
      divided: false
    },
    {
      id: 5,
      name: 'Mã sản phẩm',
      divided: true
    }
  ]
  handleSearch(key: string): void {
    this.query.search = key
    this.resetQuery()
    this.init()
  }

  init(): void {
    this.isLoading = true
    const temp = {
      ...this.query,
      contractStatus: 3,
      total: null,
      orderByClause: this.query.orderByClause ? this.query.orderByClause : null
    }
    this.api
      .getData(temp)
      .then((res: any) => {
        this.data = res.data
        this.isLoading = false
      })
      .catch(() => {
        this.isLoading = false
      })
    this.api.getContractCount(temp).then(res => {
      this.query.total = res.data
    })
  }
  handleFilter(value: Record<string, any>): void {
    this.query = {
      ...this.query,
      ...value,
      searchType: 'APPLY_FORM',
      contractSatus: 3
    }
    this.query.page = 1
    this.api
      .getData(this.query)
      .then((res: any) => {
        this.data = res.data
        this.isLoading = false
      })
      .catch(() => {
        this.isLoading = false
      })
    this.api.getContractCount(this.query).then(res => {
      this.query.total = res.data
    })
  }
  checkStatus(item: Record<string, any>): string {
    let statusText = ''
    let classStatus = ''
    if (item.contractStatus === '1') {
      statusText = 'Đang xử lý'
      classStatus = 'pending'
    } else if (item.contractStatus === '2') {
      statusText = 'Hủy'
      classStatus = 'cancel'
    } else if (item.contractStatus === '3') {
      statusText = 'Pending'
      classStatus = 'pending'
    } else if (item.contractStatus === '4') {
      statusText = 'Từ chối'
      classStatus = 'back'
    } else if (item.contractStatus === '5') {
      statusText = 'Đã phát hành'
      classStatus = 'effective'
    }

    return '<span class="' + classStatus + ' statuss">' + statusText + '</span>'
  }
  resetQuery(): void {
    this.query.page = 1
    this.query.limit = 20
  }
  getRelationShip(code: string | any): string {
    return RELATIONSHIP_VALUE_TO_TYPE[code]
  }

  handleSizeChange(size: number): void {
    this.query.limit = size
    this.init()
  }
  handleCurrentChange(page: number): void {
    this.query.page = page
    this.init()
  }
  // handleSelection(value: Array<Record<string, any>>): void {
  //   this.dataSelect = value
  // }
  handleRowClick(row: Record<string, any>): void {
    this.contractInfor = row.row
    this.setOpenPopup({
      popupName: 'contractDetail',
      isOpen: true
    })
  }
  checkType(type: string | number): string {
    let string = ''
    if (type == 1) {
      string = 'Khách hàng'
    }
    if (type == 2) {
      string = 'Người thân'
    }
    return string
  }
  handleClickFillter(): void {
    this.setOpenPopup({
      popupName: 'audit-filter',
      isOpen: true
    })
  }

  handleCommand(command: Record<string, any>): void {
    forEach(this.option, item => {
      if (command.id === item.id) {
        item.active = true
      } else {
        item.active = false
      }
    })

    this.query.orderByClause = command.id
    this.init()
  }
  indexMethod(index: number): number {
    return (this.query.page - 1) * this.query.limit + index + 1
  }

  setParamsForUser(): void {
    if (this.getDisableArea) {
      this.query.areaIdArray = `${this.user?.areaId}`
      this.query.branchIdArray = `${this.user?.branchId}`
    }
    if (this.getDisableTransaction) {
      this.query.transOfficeIdArray = `${this.user?.transactionOfficeId}`
    }
    if (this.getDisableSaleMember) {
      this.query.salesMemberIdArray = `${this.user?.id}`
    }
  }

  created(): void {
    this.api = getRepository('contract', {
      districtId: this.user?.districts?.[0].id
    })
    this.apiParam = getRepository('param', {
      districtId: this.user?.districts?.[0].id
    })
    // this.setParamsForUser()
    //@ts-ignore
    const filters = this.getFilter(this.$route.name)
    if (filters) {
      this.query = assign(this.query, filters.filters)
    }

    this.init()
  }
  handleExport(): void {
    let typeContract = 'ho_so_pending'
    this.query.contractStatus = '3'
    this.query.searchType = 'APPLY_FORM'
    let paymentName = 'contractPending'
    this.handleExportExcel(paymentName, this.query, typeContract)
  }
}
