import getRepository from '@/services'
import ExportRepository from '@/services/repositories/export'
import { Vue, Component } from 'vue-property-decorator'

@Component
export default class ExportMixin extends Vue {
  export!: ExportRepository

  isExcelLoading = false

  async handleExportExcel(paymentName: string, params: Record<string, any>, type: string): Promise<any> {
    if (params.total < 1) {
      this.$message.error('Không tìm thấy dữ liệu xuất file')
    } else if (params.total >= 50000) {
      this.$message.error('Số lượng bản ghi vượt quá 50000, vui lòng sử dụng bộ lọc')
    } else {
      this.isExcelLoading = true
      try {
        const response = await this.export.exportContract(paymentName, params)
        const url = window.URL.createObjectURL(new Blob([response.data], { type: 'application/octet-stream' }))
        const link = document.createElement('a')
        link.href = url
        const date = new Date()
        const time = `${date.getFullYear()}${date.getMonth() + 1}${date.getDate()}_${date.getHours()}${date.getMinutes()}${date.getMilliseconds()}`
        link.setAttribute('download', `Danh_sach_${type}_${time}.xlsx`)
        document.body.appendChild(link)
        link.click()
        this.isExcelLoading = false
      } catch (error) {        
        if (error.response.status === 500) {
          this.$message.error('Không tìm thấy dữ liệu xuất file')
        }
        this.isExcelLoading = false
      }
    }
  }

  created(): void {
    this.export = getRepository('export', {
      districtId: this.districtId
    })
  }
}
